.progress-bar
{
    width: 100%;
    height: 10px;
    border-radius: 10px;
    /* background: 10px; */
    background: red;
    margin: 20px 0 10px;
}
.progress-bar-fill
{
    
    height: 100%;
    border-radius: 10px;
    transition: width 0.5s ease-out;
    background-color: green;
}